html {
  font-family: 'PT Sans', sans-serif;
  color: #1d1d1d;
  font-size: 16px;
}
body {
  overflow-x: hidden;
}
b {
  font-weight: bold;
}
.c {
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1170px;
  width: 100%;
}
@media (max-width: 1199px) {
  .c {
    max-width: 768px;
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .c {
    max-width: 100%;
  }
}
.hidden {
  display: none;
}
#sprites {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  left: 0;
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 33px;
  position: relative;
  font-size: 16px;
  font-weight: bold;
  border: 2px solid #006dfe;
  height: 66px;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.btn:after {
  content: '';
  display: block;
  position: absolute;
  left: 100%;
  margin-left: -5px;
  top: 28px;
  width: 10px;
  height: 27px;
  background: #006dfe;
  transition: all 0.1s ease-in;
}
.btn svg {
  width: 28px;
  height: 13px;
  display: block;
  margin-right: 28px;
  transition: all 0.1s ease-in;
}
.btn:hover {
  color: #fff;
  background: #006dfe;
}
.btn:hover svg {
  fill: #fff;
}
.btn--lineIcon:before {
  content: '';
  display: block;
  width: 35px;
  height: 4px;
  background: #006dfe;
  margin-right: 24px;
  transition: all 0.1s ease-in;
}
.btn--lineIcon:hover:before {
  background: #1d1d1d;
}
.lines {
  position: relative;
}
.lines:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/gif;base64,R0lGODlhwAABAPABAOXl5QAAACH5BAUAAAEALAAAAADAAAEAAAINjI+py+0Po5y02htAAQA7") calc(50% - 96px) 100%;
  pointer-events: none;
}
.navBtn {
  width: 58px;
  height: 58px;
  border: 1px dashed #1d1d1d;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease-in;
}
.navBtn:hover {
  background: #1d1d1d;
  border-style: solid;
}
.navBtn svg {
  width: 30px;
  height: 12px;
}
.navBtn--next svg {
  transform: rotate(180deg);
}
.videoFrame {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  border: 10px solid #1d1d1d;
}
.videoFrame:before,
.videoFrame:after {
  content: '';
  display: block;
  position: absolute;
  height: 10px;
  background: #006dfe;
}
.videoFrame:before {
  width: 44px;
  bottom: 100%;
  left: 50%;
}
.videoFrame:after {
  width: 52px;
  top: 100%;
  right: 70px;
}
.videoFrame:hover .videoFrame__play {
  background: #1d1d1d;
}
.videoFrame:hover .videoFrame__play:before {
  background: #006dfe;
}
.videoFrame--bordered {
  border-width: 20px;
}
.videoFrame--bordered:before,
.videoFrame--bordered:after {
  height: 20px;
}
.videoFrame__preview {
  height: 100%;
  background-size: cover;
  position: relative;
}
.videoFrame__preview:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0,109,254,0) 70%, rgba(0,109,254,0.4) 100%);
}
.videoFrame__play {
  position: absolute;
  width: 193px;
  height: 94px;
  background: #006dfe;
  bottom: 0;
  right: 0;
  transition: all 0.1s ease-in;
}
.videoFrame__play:before,
.videoFrame__play:after {
  content: '';
  display: block;
  position: absolute;
}
.videoFrame__play:before {
  width: 41px;
  height: 9px;
  background: #1d1d1d;
  left: 0;
  bottom: 0;
  transition: all 0.1s ease-in;
}
.videoFrame__play:after {
  background: url("images/sprite-play.png");
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title {
  font-size: 36px;
  line-height: 48px;
}
@media (max-width: 767px) {
  .title {
    font-size: 30px;
  }
}
.title span {
  color: #006dfe;
}
.title .big {
  font-size: 48px;
  line-height: 60px;
  font-weight: bold;
  color: #006dfe;
  margin-top: 2px;
}
@media (max-width: 767px) {
  .title .big {
    margin-top: 0;
    font-size: 30px;
    line-height: 48px;
  }
}
.input {
  position: relative;
}
.input:before {
  content: '';
  display: block;
  position: absolute;
  width: 22px;
  height: 4px;
  background: #006dfe;
  top: 100%;
  margin-top: -1px;
}
.input__input {
  display: block;
  width: 100%;
  border-bottom: 1px solid #1d1d1d;
  font-size: 16px;
  color: #1d1d1d;
  padding: 10px 0;
}
.input__input--textarea {
  min-width: 100%;
  max-width: 100%;
}
.screen-reader-response {
  display: none;
}
.wpcf7-form-control-wrap {
  position: relative;
  display: block;
}
.wpcf7-not-valid-tip {
  position: absolute;
  font-size: 11px;
  color: #f00;
  top: 100%;
  margin-top: 4px;
}
.wpcf7-response-output {
  font-size: 12px;
  margin-top: 8px;
  text-align: center;
  color: #f00;
}
.wpcf7-response-output.wpcf7-mail-sent-ok {
  color: $green;
}
.header {
  position: absolute;
  padding-top: 44px;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 16px;
}
.header__container {
  position: relative;
  z-index: 3;
}
.header__container,
.header__aside,
.header__mail,
.header__address,
.header__phone {
  display: flex;
  align-items: center;
}
.header__aside {
  margin-left: auto;
}
@media (max-width: 767px) {
  .header__aside {
    display: none;
  }
}
.header__mail-icon,
.header__address-icon,
.header__phone-icon {
  fill: #1d1d1d;
