  }
}
.header__button-icon:before,
.header__button-icon:after {
  content: '';
  display: block;
  position: absolute;
  width: 53px;
  height: 1px;
  background: #fff;
  left: -27px;
}
@media (max-width: 767px) {
  .header__button-icon:before,
  .header__button-icon:after {
