.menu {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 11;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/gif;base64,R0lGODlhwAABAPABAOXl5QAAACH5BAUAAAEALAAAAADAAAEAAAINjI+py+0Po5y02htAAQA7") calc(50% - 94px) 100%, url("images/mountains.jpg") 50% 100% repeat-x, #fff;
  will-change: transform;
  transition: all 0.2s ease-in;
}
.menu--active {
  visibility: visible;
  opacity: 1;
  filter: none;
  transform: none;
}
.menu--active .menu__inner:after {
  transform: none;
}
.menu--active .menu__items {
  transform: none;
}
.menu--active .menu__title-inner {
  margin-right: -39px;
}
.menu__inner {
  position: relative;
  width: 100%;
  min-height: 100%;
}
.menu__inner:after {
  content: '';
  display: block;
  position: absolute;
  background: url("images/horse.png");
  height: 725px;
  left: 50%;
  margin-left: 370px;
  right: 0;
  bottom: 0;
  transform: translateX(70px);
  transition: all 0.2s ease-in;
}
@media (max-width: 1199px) {
  .menu__inner:after {
    margin-left: 0;
    bottom: auto;
    top: 100%;
    height: auto;
    bottom: 0;
    margin-top: -330px;
    right: 0px;
    left: auto;
    width: 620px;
  }
}
.menu__title {
  position: absolute;
  right: 50%;
  margin-right: 191px;
  overflow: hidden;
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: 1199px) {
  .menu__title {
    display: none;
  }
}
.menu__title-inner {
  margin-right: 0;
  font-size: 150px;
  font-weight: bold;
  transition: all 0.2s ease-in;
}
.menu__title-inner span {
  color: #006dfe;
}
.menu__items {
  top: 0;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-40px);
  transition: all 0.2s ease-in;
}
@media (max-width: 1199px) {
  .menu__items {
    position: static;
    left: 0;
    text-align: center;
    width: 100%;
    height: auto;
    padding-bottom: 330px;
  }
}
.menu__items ul {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.menu__items a {
  display: inline-block;
  position: relative;
  font-size: 36px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
@media (max-width: 1199px) {
  .menu__items a {
    margin-top: 55px;
  }
}
.menu__items a:after {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  margin-top: 18px;
  width: 18px;
  height: 0;
  margin-left: -2px;
  background: #1d1d1d;
  transition: all 0.1s ease-in;
}
